.flex {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.container {
  padding-bottom: 30px;
}
@media (min-width: 450px) {
  .container {
    max-width: 500px;
    -webkit-box-shadow: 2px 4px 5px rgba(0, 0, 0, 0.5);
            box-shadow: 2px 4px 5px rgba(0, 0, 0, 0.5);
    margin-top: 150px;
    padding: 20px;
    padding-bottom: 30px;
  }
}
.h1-icon {
  font-size: 34.8px;
  text-shadow: none;
}
.title-h1 {
  margin-bottom: 50px;
  text-shadow: 0px 2px 3px orange;
}
.bounce {
  -webkit-animation: bounce 1s linear 1s infinitE;
          animation: bounce 1s linear 1s infinitE;
  position: relative;
  background: pink;
  border-radius: 50%;
  display: inline-block;
}
.bounce:before,
.bounce:after {
  content: '';
  position: absolute;
  top: 18.6px;
  left: 16.3px;
  height: 7px;
  width: 7px;
  background: black;
  border-radius: 50%;
  -webkit-animation: glow 0.5s linear infinite;
          animation: glow 0.5s linear infinite;
}
.bounce:after {
  right: 16.3px;
  left: auto;
}
@-webkit-keyframes bounce {
  from {
    -webkit-transform: translatey(0px);
            transform: translatey(0px);
  }
  to {
    -webkit-transform: translatey(20px);
            transform: translatey(20px);
  }
}
@keyframes bounce {
  from {
    -webkit-transform: translatey(0px);
            transform: translatey(0px);
  }
  to {
    -webkit-transform: translatey(20px);
            transform: translatey(20px);
  }
}
@-webkit-keyframes glow {
  0% {
    -webkit-box-shadow: 0 0 0px red;
            box-shadow: 0 0 0px red;
  }
  100% {
    -webkit-box-shadow: 0 0 20px red;
            box-shadow: 0 0 20px red;
  }
}
@keyframes glow {
  0% {
    -webkit-box-shadow: 0 0 0px red;
            box-shadow: 0 0 0px red;
  }
  100% {
    -webkit-box-shadow: 0 0 20px red;
            box-shadow: 0 0 20px red;
  }
}
.pointer {
  cursor: pointer;
}
.bold {
  font-weight: bold;
}
.clear,
.search-counter {
  position: absolute;
  top: -5px;
  right: 0;
  color: teal;
  cursor: pointer;
}
.search-counter {
  right: 30px;
  top: -4px;
  width: 30px;
  height: 30px;
  background: grey;
  color: white;
  line-height: 30px;
  text-align: center;
  border-radius: 50%;
  opacity: 0;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}
.visibleCounter {
  opacity: 1;
}
.btn-block {
  width: 100%;
}
